import React from 'react';
import ReactDOM from 'react-dom';
import { Link, withRouter } from 'react-router-dom';
import $ from 'jquery';
import LoadSeed from './load_seed.js';
import logo from '../logo.svg';


//import UpcTitle from './upcTitles.js';
class VerifyItem extends React.Component {


	constructor(props) {
		var getUrl = window.location;
		var finalurl = getUrl .protocol + "//" + getUrl.hostname;
	    //console.log('1');
	    super(props);
	    this.state = {
	    baseUrl :finalurl,
	      error: null,
	      isLoaded: false,
	      showSeed: 'hide',
	      UpcTitleCheck: false,
	      MpnTitleCheck: false,
	      CondCheck: false,
	      mpnTitle:[],
	      seedVal: '1',
	      objects: [],
	      conditions: [],
	      barcodeNo:'',
	      itemType:'',
	      idS:'',
	      upcNum:'',
	      mpnName:'',
	      objName:'',
	      catId:'',
	      brandName:'',
	      mpnDesc:'',
	      remarks:'',
	      avgSold:'',
	      getCond:'',	      
	      boxHeader:'',
	      availCond:[],
	      condRadio:'',
	     // paramBarcode:this.props.barcodePass,
	      //props pasing states for seed
	      // passTtile:'',
	      // passCatId:'',
	      // passCatName:'',
	      // passDetCond:'',
	      // passListRule:'',

	    };
	    this.handleForm = this.handleForm.bind(this);
		this.handleInput = this.handleInput.bind(this);
		//this.serchUpc = this.serchUpc.bind(this);
		this.serchMpn = this.serchMpn.bind(this);
		this.serchtest = this.serchtest.bind(this);
		this.closePane = this.closePane.bind(this);

  	}

  	componentDidMount(){
  		const { barcodePass } = this.props.location.state;
  	  
    
  	 fetch(this.state.baseUrl+"/laptopzone/restgetcontroller/get_obj_drop")
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({
            isLoaded: true,
            objects: result.get_obj_itm
          });
          
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      )

      //fetch('http://localhost/laptopzone/reactcontroller/c_react/get_verify_item/'+ this.props.match.params.barcode_prv_no)
      fetch(this.state.baseUrl+'/laptopzone/reactcontroller/c_react/get_verify_item/'+ barcodePass)
      .then(res => res.json())
      .then(
        (result) => {
        	console.log(result);
        	this.setState({barcodeNo :result['get_items'][0].BARCODE_PRV_NO,
        				itemType :result['get_items'][0].BAROCDE_TYPE,
        				idS :result['get_items'][0].IDS,
        				upcNum :result['get_items'][0].UPC,
        				mpnName :result['get_items'][0].MPN,
        				catId :result['get_items'][0].CATEGORY_ID,
        				brandName :result['get_items'][0].BRAND,
        				mpnDesc :result['get_items'][0].MPN_DESC,
        				remarks :result['get_items'][0].REMARKS,
        				avgSold :result['get_items'][0].CATEGORY_ID,
        				availCond:result.get_cond,
            			CondCheck:true
            	},

        		);

          
          
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      )


      $.ajax({
            url: this.state.baseUrl+"/laptopzone/reactcontroller/c_react/get_pictures",
            dataType: 'json',
      		type: 'POST',
           data:{'barocde':this.state.barcodeNo},
            success: function(data) {
            	var dirPics = [];
              // $('#sortable').html("");
              // console.log('length:'+data.dekitted_pics.length+'');
              //console.log(data);
              for(var i=0;i<data.dekitted_pics.length;i++){
                var values = data.uri[i].split("/");
             
                var one = values[4].substring(1);

                //console.log(one);//return false;
                var picture = data.dekitted_pics[i]; 
                
                var img = '<img style="width:  220px; height: 180px;" className="sort_img up-img popup_zoom_01" id="'+data.uri[i]+'" name="dekit_image[]" src="data:image;base64,'+picture+'"/>';

                dirPics.push('<li style="width: 230px; height: 220px; background: #eee!important; float: left; overflow: hidden; text-align: center; position: relative; padding: 3px; margin:5px;" id="'+values[4]+'"> <span class="tg-li"> <div className="thumb imgCls" style="display: block; border: 1px solid rgb(55, 152, 198);  width:99%; height:99%; background: #eee!important; margin:5px;">'+img+' <input type="hidden" name="" value=""> <div className="text-center" style="width: 100px;"> <span className="thumb_delete" style="float: left;"><i title="Move Picture Order" style="padding: 3px;" className="fa fa-arrows" aria-hidden="true"></i></span> <span className="d_spn"><i title="Delete Picture" style="padding: 3px;" className="fa fa-trash specific_delete"></i></span> </div> </div> </span> </li> '); }

              $('#getsortable').html("");
              $('#getsortable').append(dirPics.join(""));

                
            }.bind(this),
            error: function(xhr, resp, text) {
                //show error to console
                console.log(xhr,resp,text);
            }
        });


  }

  	handleInput(e){
  		
		const name = e.target.name;
		
		//console.log(name);
		this.setState({[name] :e.target.value});
		// this.setState({            	
  //           	UpcTitleCheck:false,
  //           	MpnTitleCheck:false
  //       });

	}
	handleForm(e){
		e.preventDefault();
		console.log(this.state.condRadio);
		//console.log('CLICKED');
		var barcodeNo = this.state.barcodeNo;
		var itemType = this.state.itemType;
		var idS = this.state.idS;
		var idArray =[];
		idArray.push(this.state.idS);	
		var upcNum = this.state.upcNum;
		var mpnName = this.state.mpnName;
		var objName = this.state.objName;
		var catId = this.state.catId;
		var brandName = this.state.brandName;
		var mpnDesc = this.state.mpnDesc;
		var remarks = this.state.remarks;
		var avgSold = this.state.avgSold;
		

		$.ajax({
            url: this.state.baseUrl+"/laptopzone/reactcontroller/c_react/verify_item",
            dataType: 'json',
      		type: 'POST',
           data:{'barcodeNo':barcodeNo,'itemType':itemType,'idArray':idArray,'upcNum':upcNum,'mpnName':mpnName,'objName':objName,'catId':catId,'brandName':brandName,'mpnDesc':mpnDesc,'remarks':remarks,'avgSold':avgSold},
            success: function(data) {
            	if(data.exist == true){
            		

            	this.setState({
            	showSeed: 'show'
          		});

            		
            	}else if (data.exist == false){
            	this.setState({
            	showSeed: 'hide'
          		});
            		
            	}

                
            }.bind(this),
            error: function(xhr, resp, text) {
                //show error to console
                console.log(xhr,resp,text);
            }
        });

	}


	serchtest(e){
	var index = e;
    var t = document.getElementById('example2');
    var cat_id = $(t.rows[index].cells[0]).text();
    var get_title = $(t.rows[index].cells[1]).text();
    var get_mpn = $(t.rows[index].cells[2]).text();
    var get_upc = $(t.rows[index].cells[3]).text();
    var get_obj = $(t.rows[index].cells[4]).text();
    var get_brand = $(t.rows[index].cells[5]).text();
    this.setState({
            	catId: cat_id,
            	mpnDesc: get_title,
            	mpnName: get_mpn,
            	upcNum: get_upc,
            	brandName: get_brand,
          		});
  

    console.log(cat_id);


	}
	serchMpn(e){
		//e.preventDefault();
		console.log(e);
		if(e == 'up1'){
		var makeUrl = this.state.baseUrl+"/laptopzone/reactcontroller/c_react/get_upc_title";
		var passVal = this.state.upcNum;
		this.setState({
			boxHeader:'Upc'
		});
		}else if( e == 'mp1'){
		var makeUrl = this.state.baseUrl+"/laptopzone/reactcontroller/c_react/serch_mpn_sys";
		var passVal = this.state.mpnName;
		this.setState({
			boxHeader:'Mpn'
		});
		}else if( e == 'desc1'){
		var makeUrl = this.state.baseUrl+"/laptopzone/reactcontroller/c_react/serch_desc_sys";
		var passVal = this.state.mpnDesc;
		this.setState({
			boxHeader:'Description'
		});
		}		
			    
		$.ajax({
            url: makeUrl ,
            dataType: 'json',
      		type: 'POST',
           	data:{'passVal':passVal},
            success: function(data) {
            	if(data.exist == true){
            	console.log('mpn');
            	this.setState({
            	          	
            	mpnTitle:data.desc_quer,
            	MpnTitleCheck: true,  
          		});

            		
            	}else{
            	this.setState({
            	MpnTitleCheck: false
          		});

            		
            	}

                // this.setState({successUpdate: response['message']});
            }.bind(this),
            error: function(xhr, resp, text) {
                //show error to console
                console.log(xhr,resp,text);
            }
        });       
	}

	serchCond(){
		var makeUrl = this.state.baseUrl+"/laptopzone/reactcontroller/c_react/get_avail_cond";
		$.ajax({
            url: makeUrl ,
            dataType: 'json',
      		type: 'POST',
           	data:{'catId':this.state.catId},
            success: function(data) {
            	if(data.exist == true){
            	
            	this.setState({
            	          	
            	availCond:data.get_cond,
            	CondCheck:true,
          		});

            		
            	}else{
            		this.setState({
            	          	
            	availCond:data.get_cond,
            	CondCheck:true,
          		});
            		
            	}

                // this.setState({successUpdate: response['message']});
            }.bind(this),
            error: function(xhr, resp, text) {
                //show error to console
                console.log(xhr,resp,text);
            }
        }); 

	}

	closePane(){
		this.setState({         	          	
            	
            	MpnTitleCheck: false,  
          		});

	}

	

	render(){
			const { error, isLoaded, objects } = this.state;
			var isLoggedIn = this.state.showSeed;
						
			if (error) {
		      return <div> Error: {error.message}</div>;
		    } else if (!isLoaded) {
		      return (<section className="content-header">
				      <h1>
				        LOADING......
				      </h1>
				      <ol className="breadcrumb">
				        <li><a href="#"><i className="fa fa-dashboard"></i> Home</a></li>
				        <li><a href="#">Tables</a></li>
				        <li className="active">Verify Item</li>
				      </ol>
				    </section>);

		    }else{ 
		    	
		    	var ObjetVal = this.state.objects.map(function(obj) {
            		return (
                <option key={obj.OBJECT_ID} value={obj.OBJECT_ID}>{obj.OBJECT_NAME}</option>
            		);
        		});

        		

        		
		    return(		    	

			<React.Fragment>
			<section className="content-header">
			    <h1>
			      	Verify
			    	<small>Item</small>
			    </h1>
			    <ol className="breadcrumb">
			       <li><a href="#"><i className="fa fa-dashboard"></i> Home</a></li>
			        <li><a href="#">Tables</a></li>
			        <li className="active">Verify Item</li>
			    </ol>
			</section>
			<section className="content">
			    <div className="row">
			        <div className="col-xs-12">			          

				        <div className="box box-danger">
				            {/*<div className="box-header with-border">
				              <h3 className="box-title">Different Width</h3>
				            </div>*/}
				            <form onSubmit={this.handleForm}>
				            <div className="box-body">
				              	<div className="row">				              						                
					                <div className="col-xs-2">
					                  	<label>Item Type</label>
					                  	<input type="text" className="form-control" name="itemType" value={this.state.itemType} onChange= {this.handleInput} disabled></input>
					                  	<input type="hidden" className="form-control" name="idS" value={this.state.idS} onChange= {this.handleInput} disabled></input>
					                </div>
					                <div className="col-xs-2">
					                  	<label>Barcode</label>
					                  	<input type="number" className="form-control" name="barcodeNo" disabled value={this.state.barcodeNo} onChange= {this.handleInput}></input>
					                </div>
					                <div className="col-xs-2">
					                	<label>Upc</label>
					                	<div className="input-group input-group-md">
					                  	<input type="text" className="form-control" name="upcNum" value={this.state.upcNum} onChange= {this.handleInput}></input>
					                  	<div className="input-group-btn">
                        				<button className="btn btn-info " title="Search on Ebay"  type="button" onClick={()=>{this.serchMpn('up1')}}>
                        				<i className="glyphicon glyphicon-search"></i></button>
                        				</div>
					                  	</div>
					                </div>
					                <div className="col-xs-2">
					                	<label>Mpn</label>
					                	<div className="input-group input-group-md">
					                  	<input type="text" className="form-control" name="mpnName" value={this.state.mpnName} onChange= {this.handleInput}></input>
					                  	<div className="input-group-btn">
                        				<button className="btn btn-info " title="Search on Ebay"  type="button" onClick={()=>{this.serchMpn('mp1')}}>
                        				<i className="glyphicon glyphicon-search"></i></button>
                        				</div>
					                  	</div>
					                </div>
					                <div className="col-xs-2">
					                	<label>Object</label>
					                	<select className="form-control selectpicker " name="objName" value={this.state.objName} onChange= {this.handleInput} ><option value="0">Select Object.. </option>
									 	{ObjetVal}</select>
					                  	
					                </div>
					                <div className="col-xs-2">
					                	<label>Category Id</label>
					                	<div className="input-group input-group-md">
					                  	<input type="text" className="form-control" name="catId" value={this.state.catId} onChange= {this.handleInput}></input>
					                  	<div className="input-group-btn">
                        				<button className="btn btn-info " title="Search Availble Condition"  type="button" onClick={()=>{this.serchCond()}}>
                        				<i className="glyphicon glyphicon-search"></i></button>
                        				</div>
					                  	</div>
					                </div>
				              	</div>
				              	<div className="row">
					                <div className="col-xs-2">
					                  	<label>Brand</label>
					                  	<input type="text" className="form-control" name="brandName" value={this.state.brandName} onChange= {this.handleInput}></input>
					                </div>

					                <div className="col-xs-5">
					                	<label>Mpn Description</label>
					                	<div className="input-group input-group-md">
					                  	<input type="text" className="form-control" name="mpnDesc" value={this.state.mpnDesc} onChange= {this.handleInput} ></input>
					                	<div className="input-group-btn">
                        				<button className="btn btn-info " title="Search on Ebay"  type="button" onClick={()=>{this.serchMpn('desc1')}}>
                        				<i className="glyphicon glyphicon-search"></i></button>
                        				</div>
					                	</div>
					                </div>
					                <div className="col-xs-3">
					                	<label>Remarks</label>
					                  	<input type="text" className="form-control" name="remarks" value={this.state.remarks} onChange= {this.handleInput}></input>
					                </div>
					                <div className="col-xs-2">
					                	<label>Avg Sold $</label>
					                  	<input type="text" className="form-control" name="avgSold" value={this.state.avgSold} onChange= {this.handleInput}></input>
					                </div>					                
				              	</div>
				              	<div className="row">
				              	{this.state.CondCheck ? <CondVal  data = {this.state.availCond} handle  = {this.handleInput} /> :null}			    	  
				              	</div>
				              	
				              	<div className="box-footer ">                					
                					<button type="submit" className="btn btn-info pull-right">Save</button>
                					
              					</div>				              	
				            </div>	
				            </form>

				        </div>
				        <div className="row">
		                    <div className="col-sm-12">
		                        <div  className="col-sm-12 b-full">
		                         <div  className="col-sm-12 docs-galley" style={{padding_top: "12px"}} > 
		                        
		                          
		                           <ul id="getsortable" style={{color:"black", height:"auto !important", width:" 100%"}} > 
		                           
		                          </ul>
		                          
		                        
		                          </div>
		                        </div>
		                    </div>
	                  	</div>

				        				        			          
			        </div>			        
			    </div>
			    

			    
			    {this.state.MpnTitleCheck ? <ShowTitles  boxHead = {this.state.boxHeader} data ={this.state.mpnTitle} serchtest ={this.serchtest} closePane = {this.closePane} /> :null}			    	   
			    		    
      			{isLoggedIn == 'show' ? <LoadSeed passBarcode ={this.state.barcodeNo} /> :'hide'}  
      									
			    </section>
			    
			    </React.Fragment>	    

			);
		}
	}


}

function CondVal(props){	

	return (
            <div className="col-xs-12">
            <label>Available Condition</label>
            
            	{props.data.map((co,index) => (
            	<React.Fragment key ={index}>
				&nbsp;<input  type="radio" name="condRadio"  onChange= {props.handle} value={co.CONDITION_ID} ></input>
				&nbsp;<span>{co.COND_NAME}</span>
				</React.Fragment>
				
            	))}           
            </div>
        );
}



function ShowTitles(props) {

  return (
    <div className="row">
	    		<div className="col-xs-12">		          
			        <div className="box">
			        	<div className="box-header">
              				<h3 className="box-title">History Title Against ({props.boxHead})</h3>
				            <div className="box-tools pull-right">
				               <button type="button" className="btn btn-danger btn-sm"  onClick={()=>{props.closePane()}} title="Close" ><i className="fa fa-times"></i></button>
				            </div>
             
            			</div>

			            
			            <div className="box-body">
				            <table id="example2" className="table table-bordered table-hover">
				                <thead>
				                <tr>
				                  <th>Category Id</th>
				                  <th>Title</th>
				                  <th>Mpn</th>
				                  <th>Upc</th>
				                  <th>Object</th>
				                  <th>Brand</th>
				                  <th>Condition</th>
				                  <th>Select</th>
				                </tr>
				                </thead>
				                <tbody>
					                {props.data.map((tit,index) => (
	                                      <tr key={index}>
	                                          <td>{tit.CATE}</td>
	                                          <td>{tit.TITLE}</td>
	                                          <td>{tit.MPN}</td>
	                                          <td>{tit.UPC}</td>
	                                          <td>{tit.OBJECT_NAME}</td>
	                                          <td>{tit.BRAND}</td>
	                                          <td>{tit.COND_NAME}</td>	                                          
	                                          <td><button type="button" className="btn btn-danger pull-right btn-sm" onClick={()=>{props.serchtest(index+1)}}>Select</button></td>                                         
	                                          
	                                      </tr>
	                          		))}				                
				                </tbody>	                
		              		</table>
		              		
			            </div>
			            
			        </div>			          
			    </div>  
			    </div> 
  );
}




export default VerifyItem;