
import React,{Component} from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router,Route, Link,  Switch,  Redirect } from "react-router-dom";
import Box from './box_component.js';
import UnpostedRecord from './unpostedrecord.js';
import VerifyItem from './verify.js';

class Aside extends React.Component {
render(){
	return(
		<React.Fragment>
			
			<Router>
				<div className="content-wrapper">
					<aside className="main-sidebar">			    
				    	<section className="sidebar">			      			      
				      		<div className="user-panel">				        
						        <div className="pull-left image">
								    <img src="assets/dist/img/user2-160x160.jpg" className="img-circle" alt="User Image"></img>
						        </div>			        
				        		<div className="pull-left info">
				          			<p>Admin</p>
				          			<a href="#"><i className="fa fa-circle text-success"></i> Online</a>
				        		</div>				      			
				      		</div>
				      
				      			<form action="#" method="get" className="sidebar-form">
					        		<div className="input-group">
					          		<input type="text" name="q" className="form-control" placeholder="Search..."></input>
					          		<span className="input-group-btn">
					                <button type="submit" name="search" id="search-btn" className="btn btn-flat"><i className="fa fa-search"></i>
					                </button>
					              	</span>

					        		</div>
				      			</form>

					      	<ul className="sidebar-menu" data-widget="tree">
						        			        
						        <li className="treeview ">
						          <a href="#">
						            <i className="fa fa-table"></i> <span>Tables</span>
						            <span className="pull-right-container">
						                  <i className="fa fa-angle-left pull-right"></i>
						                </span> 
						          </a>
						          <ul className="treeview-menu">
						            <li className="active"><Link to="/"><i className="fa fa-circle-o"></i> box1 </Link></li>
									<li className="active"><Link to="/box2"><i className="fa fa-circle-o"></i> box2 </Link></li>
									{/*<li className="active"><Link to="/varify"><i className="fa fa-circle-o"></i> Verify </Link></li>*/}
						            
						          </ul>
						        </li>
					      	</ul>     	
				    	</section>
				    </aside>
	        				<Switch>
	          				<Route path="/" exact component={Box} />
	          				<Route path="/box2" exact component={UnpostedRecord} />
	          				<Route path="/varify" exact component={VerifyItem} />
	          				{/*<Route path="/varify/:barcode_prv_no" exact component={VerifyItem} />*/}
							</Switch>
	        	</div>      
    		</Router>
  			

			
		</React.Fragment>
		);
	}

}


export default Aside;


