import React from 'react';
import ReactDOM from 'react-dom';
import $ from 'jquery';
//import CondDrop from './dropDowns.js';


class LoadSeed extends React.Component {
	constructor(props) {		
        var getUrl = window.location;
          var finalurl = getUrl .protocol + "//" + getUrl.hostname;
	    
	    super(props);
	    this.state = {
          baseUrl :finalurl,
	      error: null,
	      isLoaded: false,
	      conditions: [],
      	  shipname: [],
      	  tempData: [],
      	  itemTitle:'',
      	  categId:'',
      	  categName:'',
      	  defCond:'',
      	  hidenCond:'',
      	  retAccept:'',
      	  defCondDis:'',
      	  listRule:'',
      	  price:'',
      	  qty:'',
      	  shipServ:'',
      	  retDay:'',
      	  otherNote:'',
      	  bin:'',
      	  editTemp:'',
      	  epId:'',
      	  hiddenItemId:'',
      	  seedUpc:'',
      	  seedMpn:'',
      	  seedBrand:'',
      	  getBarcode : this.props.passBarcode,
      	  showSpecific : false,
      	  specQueryVal : [],
      	  specificValQuery : [],
      	  arrayCount : '',
      	  arrayCat : '',
      	  custom_name : '',
      	  custom_value : '',
      	  messg:'Loading .....',
	    };
	    this.handleInput = this.handleInput.bind(this);
	    this.closeSpecific = this.closeSpecific.bind(this);
	    this.saveSpecific = this.saveSpecific.bind(this);
	    this.saveCustomSpecific = this.saveCustomSpecific.bind(this);

  	}
  	componentDidMount(){

  		console.log(this.state.getBarcode);

  		$.ajax({
            url:  this.state.baseUrl+"/laptopzone/reactcontroller/c_react/get_dropdowns",
            dataType: 'json',
      		type: 'POST',
           data:{'barocde':this.state.getBarcode,},
            success: function(data) {
            	if(data.exist == true){
            	this.setState({
	            isLoaded: true,
	            conditions: data.condition_quer,
	            shipname: data.ship_quer,
	            tempData: data.temp_data,

	            hiddenItemId:data['seed_data'][0].ITEM_ID,
	            seedUpc:data['seed_data'][0].UPC,
      	  		seedMpn:data['seed_data'][0].MFG_PART_NO,
      	  		seedBrand:data['seed_data'][0].MANUFACTURER,
	            itemTitle: data['seed_data'][0].ITEM_MT_DESC,
	            categId: data['seed_data'][0].CATEGORY_ID,
	            categName: data['seed_data'][0].CATEGORY_NAME,
	            defCond: data['seed_data'][0].DEFAULT_COND,
	            hidenCond: data['seed_data'][0].DEFAULT_COND,
	            defCondDis: data['seed_data'][0].DETAIL_COND,
	            price: data['seed_data'][0].EBAY_PRICE,	            
      	  		shipServ:data['seed_data'][0].SHIPPING_SERVICE,
      	  		bin:data['seed_data'][0].BIN_NAME,
      	  		editTemp:data['seed_data'][0].TEMPLATE_ID,
      	  		retDay:data['seed_data'][0].RETURN_DAYS,
      	  		retAccept:data['seed_data'][0].RETURN_OPTION,
      	  		otherNote:data['seed_data'][0].OTHER_NOTES,
      	  		qty:data['list_qty'][0].QTY,
      	  		
	          	});
            		

            		
            	}else{
            	this.setState({
            	isLoaded: false
          		});
            		
            	}

                
            }.bind(this),
            error: function(xhr, resp, text) {
                //show error to console
                console.log(xhr,resp,text);
            }
        });

        $.ajax({
            url:  this.state.baseUrl+"/laptopzone/reactcontroller/c_react/get_pictures",
            dataType: 'json',
      		type: 'POST',
           data:{'barocde':this.state.getBarcode,},
            success: function(data) {
            	if(data.exist == true){
            	this.setState({
      	  		
	          	});            		
            	}else{
            	
            		
            	}

                
            }.bind(this),
            error: function(xhr, resp, text) {
                //show error to console
                console.log(xhr,resp,text);
            }
        });
  }
  	handleInput(e){
		const name = e.target.name;
		//console.log(name);
		this.setState({[name] :e.target.value})

	}
  	handleForm(){
  	//preventDefault();
  	var seedUpc = this.state.seedUpc;
  	var seedMpn = this.state.seedMpn;
  	var seedBrand = this.state.seedBrand;
  	var itemTitle = this.state.itemTitle;
  	var categId = this.state.categId;
  	var categName = this.state.categName;
  	var defCond = this.state.defCond;
  	var defCondDis = this.state.defCondDis;
  	var hidenCond = this.state.hidenCond;
  	var price = this.state.price;
  	var shipServ = this.state.shipServ;
  	var bin = this.state.bin;
  	var editTemp = this.state.editTemp;
  	var retDay = this.state.retDay;
  	var retAccept = this.state.retAccept;
  	var otherNote = this.state.otherNote;

	this.setState({ 
            isLoaded: false,          	
			
			});
  	
  		$.ajax({
            url:  this.state.baseUrl+"/laptopzone/reactcontroller/c_react/update_seed",
            dataType: 'json',
      		type: 'POST',
           	data:{'barocde':this.state.getBarcode,'seedUpc':seedUpc,'seedMpn':seedMpn,'seedBrand':seedBrand,'itemTitle':itemTitle,'categId':categId,'categName':categName,
           	'defCond':defCond,'defCondDis':defCondDis,'hidenCond':hidenCond,'price':price,'shipServ':shipServ,'bin':bin,'editTemp':editTemp,'retDay':retDay,'retAccept':retAccept,'otherNote':otherNote},
            success: function(data) {
            	if(data){
            	this.setState({ 
            	 isLoaded: true,       	          	
            	 hidenCond: this.state.defCondDis,       	          	
            	 messg: 'Updating Seed ...',       	          	
            	
            	
          		});      		

            		
            	}else {
            	this.setState({
            	isLoaded: true
          		});
            		
            	}

                
            }.bind(this),
            error: function(xhr, resp, text) {
                //show error to console
                //console.log(xhr,resp,text);
            }
        });

  	//console.log('hello');

  	}

  	updateSpecific(){

  		
  		this.setState({ 
            	 showSpecific: true	
            	
          		}); 
  		$('#spec').focus();

  		$.ajax({
            url:  this.state.baseUrl+"/laptopzone/reactcontroller/c_react/queryData",
            dataType: 'json',
      		type: 'POST',
           	data:{'barocde':this.state.getBarcode},
            success: function(data) {
            	if(data){
            	
            	var count  = data.mt_id.length;
            	console.log(data.mt_id);
            	console.log(data.specs_qry);
            	this.setState({ 
            	specQueryVal:data.mt_id,
            	specificValQuery:data.specs_qry,
            	arrayCount:count,
            	arrayCat:data['mt_id'][0].EBAY_CATEGORY_ID,
            	showSpecific: true,
          		});      		

            		
            	}else {
            	this.setState({
            	showSpecific: false, 
          		});
            		
            	}

                
            }.bind(this),
            error: function(xhr, resp, text) {
                //show error to console
                //console.log(xhr,resp,text);
            }
        });


  	}

  	saveSpecific(){
  	
  		
 	var count = $('#arrayCount').val(); 
 		
	var item_id = this.state.hiddenItemId;
	var item_mpn =	this.state.seedMpn;
	var item_upc = this.state.seedUpc;
	var cat_id = this.state.categId;
	//alert(item_id+count+item_mpn+item_upc+cat_id);

	var spec_name=[];
  	var spec_value=[];

  	for (var i = 1; i <= count; i++) { 
    
     spec_name.push($('#specific_name_'+i).text()); 
     spec_value.push($('#specific_'+i).val());

  	}
  	console.log(spec_name);
  	console.log(spec_value);

  
      $.ajax({
        dataType: 'json',
        type: 'POST',        
        url: this.state.baseUrl+"/laptopzone/reactcontroller/c_react/add_specifics",
        
        
        data: { 
          'item_id' : item_id,
          'cat_id' : cat_id,
          'spec_name' : spec_name,
          'spec_value' : spec_value,
          'item_upc' : item_upc,
          'item_mpn' : item_mpn
      },
       success: function (data) {
         if(data != ''){
          alert('Item Specific Updated');
          return false;
          //window.location.href = '<?php echo base_url(); ?>specifics/c_item_specifics';
                   
         }else{
           alert('Error:UPC & MPN not found');
           return false;
           //window.location.href = '<?php echo base_url(); ?>specifics/c_item_specifics';
         }
       }
      }); 
 
  	}

  	saveCustomSpecific(){
  		


  	var cat_id = this.state.categId;

    var custom_name = this.state.custom_name;

    var custom_value = this.state.custom_value;
    var maxValue = $("#maxValue").val();
    var selectionMode = $("#selectionMode").val();
    var catalogue_only = $("#catalogue_only").val();

   

  if(custom_name == '' || custom_name == null){
    alert('Please insert specific name');
    $("#custom_name").focus();
    return false;
  }else if(custom_value == '' || custom_value == null){
    alert('Please insert specific value');
    $("#custom_value").focus();
    return false;
  }
  // this.setState({ 
  //           	 showSpecific: false	
            	
  //         		});

    $.ajax({
      dataType: 'json',
      type: 'POST',        
      url: this.state.baseUrl+"/laptopzone/reactcontroller/c_react/add_custom_specifics",

      data: { 'cat_id' : cat_id,
              'custom_name' : custom_name,
              'custom_value' : custom_value
    },
     success: function (data) {
     	// this.setState({ 
      //       	 showSpecific: true	
            	
      //     		});
        if(data == false){
          alert('Specific name is already exist.');
          
          return false;
          }else{
          
           alert('Record successfully added.');
            
            return false;
          }          
     }
      }); 
  	}

  	closeSpecific(){
  		this.setState({ 
            	 showSpecific: false	
            	
          		});

  	}


  	render(){  		
  			var that =this;
  			const { error, isLoaded, conditions } = this.state;
			if (error) {
		      	return <div> Error: {error.message}</div>;
		    	}else if (!isLoaded) {
		      	return <div><h1>{this.state.messg}</h1></div>;
		    	}else {
		    	

		    	var conditionValues = this.state.conditions.map(function(cond) {
		            return (
		                <option key={cond.ID} value={cond.ID}>{cond.COND_NAME}</option>
		            );
		        });
		         var shipValues = this.state.shipname.map(function(ship) {
		            return (
		                <option key={ship.SHIPING_NAME} value={ship.SHIPING_NAME}>{ship.SHIPING_NAME}</option>
		            );
		        });

		        var tempVal = this.state.tempData.map(function(temp) {
		            return (
		                <option key={temp.TEMPLATE_ID} value={temp.TEMPLATE_ID}>{temp.TEMPLATE_NAME}</option>
		            );

		        });

		        

			
			var makeSpec =this.state.specQueryVal.map(function(count,index) {
				var i = index+1;
				
				var id2= "specific_name_"+i;
				var selectName= "specific_"+i;
				var inDiv =  "specific"+count.MT_ID;
				
				return(	
							
				<div key = {index} className="col-sm-3">

				
				
				
				<label  id ={id2} >{count.SPECIFIC_NAME}</label>
				

				<select className="form-control  "  id ={selectName}><option  value="select">------------Select------------</option>
				{
			          that.state.specificValQuery.map((speci,index) => {

			          	if(speci.MT_ID == count.MT_ID){

			            return(
			            <React.Fragment>
			            
			            
			            <option key = {index} value={speci.SPECIFIC_VALUE} >{speci.SPECIFIC_VALUE}</option>
			            </React.Fragment>
			            	)
			    		}
			          })
			    }
			    </select>
				
				</div>
				
				)
			 

			});	       


		        return (
		        <React.Fragment>
		        <div className="row">
			        <div className="col-xs-12">
			          
			        		<h3 align="center">Item Seed..</h3>
				        <div className="box box-info">
				            {/*<div className="box-header with-border">
				              <h3 align="center">Item Seed</h3>
				            </div>*/}
				            				            
					            <div className="box-body">
					              	<div className="row">
					              		<div className="col-xs-4">
						                  	<label>Seed Upc:</label>
						                  	<input type="hidden" className="form-control" name="hiddenItemId" value={this.state.hiddenItemId} onChange= {this.handleInput} ></input>
						                  	<input type="text" className="form-control" name="seedUpc" value={this.state.seedUpc} onChange= {this.handleInput} ></input>
						                </div>
						                <div className="col-xs-4">
						                  	<label>Seed Mpn:</label>
						                  	<input type="text" className="form-control" name="seedMpn" value={this.state.seedMpn} onChange= {this.handleInput} ></input>
						                </div>
						                <div className="col-xs-4">
						                  	<label>Seed Brand:</label>
						                  	<input type="text" className="form-control" name="seedBrand" value={this.state.seedBrand} onChange= {this.handleInput} ></input>
						                </div>					                
						                
					              	</div>
					              	<div className="row">
					              		<div className="col-xs-12">
						                  	<label>Master Pictures:</label>
						                  	<input type="text" className="form-control" name="" value="" ></input>
						                </div>					                
						                
					              	</div>
					              	<div className="row">
					              		<div className="col-xs-12">
						                  	<label>Dekit Remarks:</label>
						                  	<input type="text" className="form-control" name="" value="" ></input>
						                </div>					                
						                
					              	</div>
					              	<div className="row">
					              		<div className="col-xs-12">
						                  	<label>Picture Notes:</label>
						                  	<input type="text" className="form-control" name="" value="" ></input>
						                </div>					                
						                
					              	</div>
					              	<div className="row">
					              		<div className="col-xs-12">
						                  	<label>Title:</label>
						                  	<input type="text" className="form-control" name="itemTitle" value={this.state.itemTitle} onChange= {this.handleInput} ></input>
						                </div>					                
						                
					              	</div>
					              	<div className="row">
					              		<div className="col-xs-2">
						                  	<label>Category Id:</label>
						                  	<input type="number" className="form-control" name="categId" value={this.state.categId} onChange= {this.handleInput} ></input>					                  	
						                </div>
						                <div className="col-xs-4">
						                  	<label>Category Name:</label>
						                  	<input type="name" className="form-control" name="categName"  value={this.state.categName} onChange= {this.handleInput} ></input>
						                  	
						                </div>
						                <div className="col-xs-3">
						                  	<label>Default Condition:</label>
						                  	<input type="hidden" className="form-control" name="hidenCond"  value={this.state.hidenCond} onChange= {this.handleInput} ></input>
						                  	<select className="form-control selectpicker " name="defCond" value={this.state.defCond} onChange= {this.handleInput} >
						                  	<option value="0">Select Condiotion.. </option>
										 	{conditionValues}
										 	</select>
						                </div>
						                <div className="col-xs-3">
						                  	<label>Return Accepted:</label>
						                  	<select className="form-control selectpicker " name="retAccept" value={this.state.retAccept} onChange= {this.handleInput} ><option value="0">Select Option.. </option>
										 	<option value="ReturnsAccepted">Yes</option>
										 	<option value="ReturnsNotAccepted">No</option>
										 	</select>					                  	
						                </div>				                
						                
					              	</div>
					              	<div className="row">
					              		<div className="col-xs-12">
					              		<label>Default Condition Description:</label>
						                <textarea style={{color: "red"}} type="text" className="form-control" name="defCondDis" value={this.state.defCondDis} onChange= {this.handleInput}  ></textarea>
						                </div>
					              	</div>	

					              	<div className="row">
					              		<div className="col-xs-12">
					              		<label>Listing Rule:</label>
						                <textarea type="text" className="form-control" name="listRule" value={this.state.listRule} ></textarea>
						                </div>
					              	</div>
					              	<div className="row">				              
					              		<div className="col-xs-2 ">				              			
					              			<label>Price:</label>
					              			<div className="input-group ">
					              			<span className="input-group-addon">US $</span>					                  	
						                  	<input type="number" step="0.01" min="0" className="form-control" name="price" value={this.state.price} onChange= {this.handleInput} ></input>
						                  	</div>
						                </div>          
						                <div className="col-xs-2">
						                  	<label>QTY:</label>
						                  	<input type="number" className="form-control" name="qty" disabled value={this.state.qty} onChange= {this.handleInput}  ></input>
						                </div>
						                <div className="col-xs-2">
						                  	<label>Hold Barcode:</label>
						                  	<input type="text" className="form-control" name="" value="" ></input>
						                </div>
						                <div className="col-xs-2">
						                  	<label>Bin:</label>
						                  	<input type="text" className="form-control" name="bin" value={this.state.bin} onChange= {this.handleInput} ></input>
						                </div>
						                <div className="col-xs-4">
						                  	<label>Other Notes:</label>
						                  	<input type="text" className="form-control" name="otherNote" value={this.state.otherNote} onChange= {this.handleInput} ></input>
						                </div>					                
					              	</div>
					              	<div className="row">				              		
						                <div className="col-xs-4">
						                  	<label>Shipping Service:</label>
						                  	<select className="form-control selectpicker " name="shipServ" value={this.state.shipServ} onChange= {this.handleInput} >
						                  	<option value="0">Select Shipping.. </option>
										 	{shipValues}
										 	</select>
						                  	
						                </div>
						                <div className="col-xs-2">
						                  	<label>Return In Days:</label>
						                  	<select className="form-control selectpicker " name="retDay" value={this.state.retDay} onChange= {this.handleInput} >
						                  	<option value="14">14 Days</option>
	                    					<option value="30">30 Days</option>
	                    					<option value="60">60 Days</option>
										 	</select>					                  	
						                </div>
					              		
						                <div className="col-xs-3">
						                  	<label>Edit Templete:</label>
						                  	<select className="form-control selectpicker " name="editTemp" value={this.state.editTemp} onChange= {this.handleInput} >
						                  	<option value="0">Select Shipping.. </option>
										 	{tempVal}
										 	</select>
						                </div>
						                <div className="col-xs-3">
						                  	<label>Epid:</label>
						                  	<input type="number" className="form-control" name="epId" value={this.state.epId} onChange= {this.handleInput}></input>
						                </div>

					              	</div>			              	
					              					              	
					            </div>
					            <div className="row">
					            <div className="col-xs-12">
					            <div className="box-footer" align="center">                					
	                					<button   onClick={()=>{this.handleForm()}} className="btn btn-success pull-left">Update</button>               					
	                					<button   onClick={()=>{this.updateSpecific()}} className="btn btn-warning pull-left">Update Specifics</button>               					
	                					<button   className="btn btn-danger pull-right">List</button>               					
	                					
	              				</div>
	              				</div>
					            </div>
				            		            
				        </div>			          
			        </div>
			        
			    </div>
			    {this.state.showSpecific ? 
			    	<div className="row">
			    	<div className="col-sm-12">
			    	<div className="box box-warning">
			    			<div className="box-header with-border">
				              <h3 className="box-title">Item Specifics </h3>
				              <div className="box-tools pull-right">
				               <button type="button" className="btn btn-danger btn-sm"  onClick={()=>{this.closeSpecific()}} title="Close" ><i className="fa fa-times"></i></button>
				            </div>
				            </div>
				            
			    		<div className="box-body">
				    		<div className="row">
				    		<div className="col-sm-12">
				    		

				    		<input type ="hidden" name ="arrayCount" id ="arrayCount" value = {this.state.arrayCount}></input>
				    		<input type ="hidden" name ="arrayCat" id ="arrayCat" value = {this.state.arrayCat}></input>
					        {makeSpec}				        
				    		</div>  
				    		</div>


				    		<div className="row">
					    		<div className="col-xs-12">
					        	<div className="box-footer" align="right">                					
	                			<button   type="button" className="btn btn-success pull-right" onClick={()=>{this.saveSpecific()}}>Save</button>	                					
	              				</div>
	              				</div>
							</div>
							<br></br>
							<div className="row">
					    		<div className="col-xs-3">
						        	                					
		                		<h4>Add your own item specific</h4>				            
		              				
	              				</div>
	              				<div className="col-xs-3">
					        	                					
	                			
					            <input type="text" className="form-control" placeholder="Enter item specific name"  id ="custom_name" name="custom_name"  value={this.state.custom_name} onChange= {this.handleInput}></input>	                					
	              				<span>For example, Brand, Material, or Year</span>
	              				</div>
	              				<div className="col-xs-3">
					        	                					
	                			
					            <input type="text" className="form-control" placeholder="Enter item specific value"  id ="custom_value" name="custom_value"  value={this.state.custom_value} onChange= {this.handleInput}></input>
	              				<span>For example, Ty, plastic, or 2007</span>
	              				</div>
	              				<div className="col-xs-3">
					        	                					
	                			<button type="button" className="btn btn-success " onClick={()=>{this.saveCustomSpecific()}} >Save</button>	                					
	              				
	              				</div>
							</div>  
			    		</div>  
			    	</div> 

			    	</div>
			    	</div>
			    	
			    	  
			    	

			    	 
			    :null}
			    

		        </React.Fragment>
		        );
	    	}
  				
			
  	}

}

export default LoadSeed;