
import React from 'react';
import ReactDOM from 'react-dom';
import { Link } from 'react-router-dom';
import $ from 'jquery';


class UnpostedRecord extends React.Component {

	constructor(props) {
	var getUrl = window.location;
	var finalurl = getUrl .protocol + "//" + getUrl.hostname;
    //console.log(getUrl);
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
      baseUrl :finalurl,
    };
    
  }

  	componentDidMount(){   

    //console.log(finalurl);

  	fetch(this.state.baseUrl+"/laptopzone/restgetcontroller/get_unposted_items")
      .then(res => res.json())
      .then(
        (result) => {
          //console.log(result);
          this.setState({
            isLoaded: true,
            items: result.get_items
          });
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          this.setState({
            isLoaded: false,
            error
          });
        }
      )
  	}
  	


   

render()
	{	
		const { error, isLoaded, items } = this.state;		
		if (error) {
	      return <div> Error: {error.message}</div>;
	    } else if (!isLoaded) {
	      return (<section className="content-header">
			      <h1>
			        LOADING......
			      </h1>
			      <ol className="breadcrumb">
			        <li><a href="#"><i className="fa fa-dashboard"></i> Home</a></li>
			        <li><a href="#">Tables</a></li>
			        <li className="active">Unposted Items</li>
			      </ol>
			    </section>);

	    } else {
	    return (
			<React.Fragment>    
			    <section className="content-header">
			      <h1>
			        Item
			        <small>Recognition</small>
			      </h1>
			      <ol className="breadcrumb">
			        <li><a href="#"><i className="fa fa-dashboard"></i> Home</a></li>
			        <li><a href="#">Tables</a></li>
			        <li className="active">Unposted Items</li>
			      </ol>
			    </section>

	    
			    <section className="content">
			      <div className="row">
			        <div className="col-xs-12">
			          

			          <div className="box">
			            <div className="box-header">
			              <h3 className="box-title">Unposted Items</h3>
			            </div>
			            
			            <div className="box-body">
				            <table id="example2" className="table table-bordered table-hover">
				                <thead>
				                <tr>
				                  <th>Action</th>
				                  <th>Barcode No</th>
				                  <th>Condition</th>
				                  <th>Object</th>
				                  <th>Mpn Desc</th>
				                  <th>Mpn</th>
				                  <th>Upc</th>
				                  <th>Pic Date</th>
				                  <th>Pic By</th>
				                  <th>Remarks</th>
				                  <th>Folder Name</th>
				                  <th>Item Type</th>
				                </tr>
				                </thead>
				                <tbody>
					                {items.map(item => (

 

	                                      <tr key={item.BARCODE_PRV_NO}>
	                                          {/*<td><Link to={`/varify/${item.BARCODE_PRV_NO}`}><button className="btn btn-info pull-left">Edit</button></Link></td>*/}


<td><Link to={{ pathname:"/varify",state: { 
        barcodePass:item.BARCODE_PRV_NO 
    } 
}} ><button className="btn btn-info pull-left">Edit</button></Link></td>

	                                          <td>{item.BARCODE_PRV_NO}</td>
	                                          <td>{item.COND_NAME}</td>
	                                          <td>{item.OBJECT_NAME}</td>
	                                          <td>{item.MPN_DESC}</td>
	                                          <td>{item.MPN}</td>
	                                          <td>{item.UPC}</td>
	                                          <td>{item.PIC_DATE_TIME}</td>
	                                          <td>{item.PIC_BY}</td>
	                                          <td>{item.REMARKS}</td>
	                                          <td>{item.FOLDER_NAME}</td>	                                          
	                                          <td>{item.BAROCDE_TYPE}</td>	                                          
	                                          
	                                      </tr>
	                          		))}				                
				                </tbody>	                
		              		</table>			              
			            </div>
			            
			          </div>
			          
			        </div>
			        
			      </div>
			      
			    </section>
	    
	  		
	  		
	  		</React.Fragment>
	  		
			);
		}
	}
}
export default UnpostedRecord;